#top-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: white;
  background-color: red;
  white-space: none;
}

.logo > img {
  min-width: 100px;
  min-height: auto;
  position: relative;
  left: -400px;
}

.logo {
  display: inline-block;
  position: relative;
}

.title {
  display: inline-block;
}

.bottom-nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  height:auto;
  width: 40px;
  padding: 6px;
}

.contact {
  display:flex;
  justify-content: center;
  align-items: center;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.button {
  display: flex;
  justify-content: flex-end;
  min-width: 100%;
  position: relative;
  top: 5px;
}
